import { CommonModule } from "@angular/common";
import { HttpClientModule } from "@angular/common/http";
import { Router, RouterModule } from "@angular/router";
import { Component } from '@angular/core';
import { User, UserService } from "../../services/user.service";
import { Observable } from "rxjs";
@Component({
  selector: 'app-user-header',
  standalone: true,
  imports: [CommonModule, RouterModule],
  templateUrl: './user-header.component.html',
  styleUrls: ['./user-header.component.scss']
})
export class UserHeaderComponent {
  dropdownOpen = false;
  user: User | null = null;
  ngOnInit(): void {
    this.userService.getUser().subscribe(user => {
      this.user = user;
    });
  }
  constructor(
    private router: Router,private userService: UserService
  ) {}

  toggleDropdown() {
    this.dropdownOpen = !this.dropdownOpen;
  }

  logout(): void {
    localStorage.clear();
    this.router.navigate(['']);
  }

    
getInitials(user: User | null): string {
  if (!user?.doptOfficer?.englishName) return 'NA';

  // ✅ List of honorifics / prefixes to ignore
  const ignoreList = ['shri', 'mr', 'mrs', 'ms', 'dr', 'prof', 'sir'];

  // ✅ Clean up the name
  const names = user.doptOfficer.englishName
    .trim()
    .split(/\s+/)
    .filter((n: string) => !ignoreList.includes(n.toLowerCase()));

  if (names.length === 0) return 'NA';

  let initials = '';
  
  if (names.length === 1) {
    // Single word → take first 2 letters
    initials = names[0].substring(0, 2).toUpperCase();
  } else {
    // Multi-word → take first and last
    initials = names[0][0].toUpperCase() + names[names.length - 1][0].toUpperCase();
  }

  return initials;
}

}
